<!--  -->
<template>
  <div>
    <!-- <a-card title="多个组件传值">
      <child-a></child-a>
      <child-b></child-b>
      <child-c @sendA="senA"></child-c>
    </a-card> -->

    <div class="box">
      <p>a-radio-group+table</p>
      <a-radio-group
        @change="onCategoryChange($event)"
        v-model="categoryId"
        class="radio-group-box"
        button-style="solid"
      >
        <a-table size="small" :columns="categoryColumns" :dataSource="loadCategoryData" rowKey="id">
          <span slot="action" slot-scope="text, record">
            <a-radio-button :value="record.id" />
          </span>
        </a-table>
      </a-radio-group>
    </div>

    <div class="box">
      <p>组件传值</p>
      <!-- 前者自定义名称便于子组件调用，后者要传递数据名 -->
      <div>
        <p>testText ：{{testText}}</p>
        <a-button @click="clear">清空</a-button>
      </div>

      <a-card title="子组件">
        <child :test="test" @ok="handleOk"></child>
      </a-card>
    </div>
  </div>
</template>

<script>
import Child from "./table/Child";
// import ChildA from "../../components/ChildA";
// import ChildB from "../../components/ChildB";
// import ChildC from "../../components/ChildC";

export default {
  data() {
    return {
      categoryId: -1,
      categoryColumns: [
        {
          title: "id",
          dataIndex: "id",
        },
        {
          title: "名称",
          dataIndex: "title",
        },
        {
          title: "选择",
          dataIndex: "action",
          scopedSlots: { customRender: "action" },
        },
      ],
      loadCategoryData: [
        {
          id: 1,
          title: "a",
        },
        {
          id: 2,
          title: "b",
        },
      ],
      test: ["apple", "orange", "peach"],
      testText: "",
    };
  },

  props: {},

  components: {
    Child,
    // ChildA,
    // ChildB,
    // ChildC
  },

  created() {
  },

  computed: {},

  mounted() {},

  methods: {
    onCategoryChange(e) {
      console.log(e.target.value);
    },

    handleOk(e) {
      this.testText = e;
    },

    clear(){
      this.testText=''
    },
    // senA(){
    //   console.log('sena--')
    // }
  },
};
</script>
<style>
.box {
  margin:20px auto;
  width: 800px;
}
.radio-group-box {
  width: 800px;
}
</style>
