import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import About from '../views/About.vue'
import A from '../views/A'
import Lang from '../views/lang'

Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/about',
    name: 'About',
    component: About
  },
  {
    path: '/A',
    name: 'A',
    component: A
  },{
    path:'/lang',
    name:'lang',
    component:Lang
  }
]

const router = new VueRouter({
  routes
})

export default router
