<!--  -->
<template>
  <div style="width: 800px; margin: 0 auto">
    <p class="title">
      点击按钮切换语言——>>
      <a-button
        @click="changeLangEvent"
        v-if="$i18n.locale == 'zh-CN'"
        class="lang-btn"
        >中文</a-button
      >
      <a-button v-else @click="changeLangEvent" class="lang-btn"
        >English</a-button
      >
    </p>

    <a-card title="切换语言测试文本" style="width: 800px; margin: 20px auto">
      <p class="title">
        在vue-cli构建的项目中使用i18n，实现策略：准备了两套语言的文本进行切换。
      </p>
      <ul v-if="$i18n.locale == 'zh-CN'">
        <li>
          Vue I18n 是 Vue.js
          的国际化插件。它可以轻松地将一些本地化功能集成到你的 Vue.js
          应用程序中。
        </li>
        <li>
          如果使用模块系统 (例如通过 vue-cli)，则需要导入 Vue 和 VueI18n
          ，然后调用 Vue.use(VueI18n)。
        </li>
      </ul>
      <ul v-else>
        <li>
          Vue I18N is Vue.js International plug-ins for. It can easily integrate
          some localization features into your Vue.js Application.
        </li>
        <li>
          If you use the module system (for example, through vue-cli), you need
          to import Vue and VueI18n, and then call. Vue.use (VueI18n)。
        </li>
      </ul>
    </a-card>

    <a-card style="width: 800px; margin: 20px auto" title="切换语言包">
      <ul class="menu-ul">
        <li>{{ $t("lang.homeOverview") }}</li>
        <li>{{ $t("lang.firmOverview") }}</li>
        <li>{{ $t("lang.firmReports") }}</li>
        <li>{{ $t("lang.firmAppendix") }}</li>
        <li>{{ $t("lang.firmIndex") }}</li>
        <li>{{ $t("lang.firmAnalysis") }}</li>
        <li>{{ $t("lang.firmNews") }}</li>
        <li>{{ $t("lang.firmOther") }}</li>
      </ul>
    </a-card>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },

  props: {},

  components: {},

  created() {},

  computed: {},

  mounted() {},

  methods: {
    changeLangEvent() {
      this.$confirm({
        title: "确定切换语言吗?",
        okText: "确定",
        cancelText: "取消",
        onOk: () => {
          if (this.$i18n.locale === "zh-CN") {
            this.$i18n.locale = "en-US"; //关键语句
          } else {
            this.$i18n.locale = "zh-CN"; //关键语句
          }
          console.log("this.$i18n.locale---", this.$i18n.locale);
        },
        onCancel: () => {
          // if (this.$i18n.locale === "zh-CN") {
          //   this.$i18n.locale = "en-US"; //关键语句
          // } else {
          //   this.$i18n.locale = "zh-CN"; //关键语句
          // }
          // console.log("this.$i18n.locale---", this.$i18n.locale);
        },
      });
    },
  },
};
</script>
<style>
.lang-btn {
  margin-left: 20px;
}
.title {
  color: cornflowerblue;
  font-weight: bold;
  text-align: left;
}
.menu-ul {
  display: flex;
  padding: 0;
}
.menu-ul li {
  padding: 10px 5px;
  text-align: center;
  border: 1px solid #666;
  box-sizing: border-box;
  border-right: 0;
}
.menu-ul li:last-child{
    border-right: 1px solid #666;
}
li {
  list-style: none;
}
</style>
